.bar {
  display: -ms-flexbox;
  display: flex;
  height: 24px;
}

.bar__desc {
  --border: 1px solid transparent;
  width: 64px;
  height: 24px;
  padding: 3px;
  display: -ms-flexbox;
  display: flex;
  border-top: var(--border);
  border-left: var(--border);
  border-bottom: var(--border);
  -ms-flex-align: baseline;
      align-items: baseline;
}

.bar__desc--secondary {
  margin-left: 3px;
  font-size: 11px;
  color: rgba(0,0,0,.54);
}

.bar__column {
  --border: 1px solid #80D8FF;
  background: #c2e6ed;
  -webkit-transition: all;
  -o-transition: all;
  transition: all;
  -webkit-transition-duration: 300ms;
       -o-transition-duration: 300ms;
          transition-duration: 300ms;
  height: 24px;
  opacity: 0;
  border-top: var(--border);
  border-right: var(--border);
  border-bottom: var(--border);
  -webkit-transition: all 200ms;
  -o-transition: all 200ms;
  transition: all 200ms;
}

.bar__caption {
  -ms-flex-item-align: center;
      align-self: center;
  padding-left: 3px;
  font-size: 14px;
  width: 45px;
  color: rgba(0,0,0,.54);
}

/* hover state */

.bar:hover .bar__column {
  background: #80D8FF;
}

.bar:hover .bar__desc {
  --border-hover: 1px solid #80D8FF;
  border-top: var(--border-hover);
  border-left: var(--border-hover);
  border-bottom: var(--border-hover);
}



